import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Ema from '../views/EmaIndex.vue'
import Triangle from '../views/Triangle.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Triangle,
    },
    {
        path: '/blog/ema',
        name: 'ema_index',
        component: Ema,
    },
    {
        path: '/blog/triangle',
        name: 'triangle_index',
        component: Home,
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
})

export default router
